export default {
	props:{
		DataList:{
			type:Array
		},
		url:{
			type:String
		}
	},
	data() {
		return {
			
		}
	},
	methods: {
		handleNavigator(item){
			let url=this.url;
			if(item.score != undefined){
				url = this.url+'?score='+item.score
			}
			uni.navigateTo({
				url:url
			})
		}
	}
}
